export default {
	data() {
		return {
			goodsList: [], // 商品列表大数组
			filterby: "all", // 默认选择的tab
			name:'',
			page: 1,
			size: 6,
			loadingText: "正在加载....",
			filterByList: [{
					text: "全部",
					selected: true,
					filterby: "all"
				},
				{
					text: "好货",
					selected: false,
					filterby: "public"
				},
				{
					text: "热门",
					selected: false,
					filterby: "hot"
				}
			]
		}
	},
	methods: {
		handleGoods(goods){
			//页面跳转 商品详情  把当前点击商品数据传过去
			uni.navigateTo({
				url:'./goods?goodsInfo='+JSON.stringify(goods)
			})
		},
		handleSelect(index) {
			// 其他的selected false
			for (let i = 0; i < this.filterByList.length; i++) {
				if (i != index) {
					this.filterByList[i].selected = false;
				}
			}
			this.filterByList[index].selected = true;
			
			// 不同选项卡数据请求 this.filterByList[index].filterby为全部求全部数据 为热门请求热门数据
			this.filterby = this.filterByList[index].filterby;
			this.page = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
		},
		loadData() {
				this.request({
					url: 'https://www.wudongming.top/uni/type_list/'+this.name+'/'+this.page+'/'+this.size,
					success: ((res) => {
						/* this.goodsList = res.data
						console.log(this.goodsList) */
						if(res.data.length <= 6){
							this.loadingText = "到底了";
						}
						
						if(res.data.length > 0){
							//全部
							res.data.forEach(item => {
								this.goodsList.push(item);
							})
							//好货
							if(this.filterby == "public"){
							this.goodsList = []
							var result = res.data.filter(item => {
									return item.public == true
								})
								result.forEach(item => {
								this.goodsList.push(item);
							})
							return
							}
							//热门
							if(this.filterby == "hot"){
							this.goodsList = []
							var result = res.data.filter(item => {
									return item.hot == true
								})
								result.forEach(item => {
								this.goodsList.push(item);
							})
							return
							}
						}else{
							this.loadingText = "到底了";
						}
					})
				})
		},
	onLoad(option) {
		 //console.log(option.name)
		// 动态修改nav title
		this.name = option.name
		uni.setNavigationBarTitle({
			title: option.name
		})
		// 加载数据
		this.loadData();
	},  //下拉重新加载
	onPullDownRefresh(){
		setTimeout(() => {
			this.page = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
			//停止刷新图标
			uni.stopPullDownRefresh();
		},1000)
	},
	// 上啦加载 页数+1
	onReachBottom(){
		this.page++;
		this.loadData();
	}
}
}
